.wrap {
    position: relative;
    box-sizing: border-box;
    width: 100%;
    max-width: 100%;
}
.input {
    padding: 0 15px;
    -webkit-border-radius: 4px;
    -moz-border-radius: 4px;
    border-radius: 4px;
    border: 1px solid #000;
}

.ML__keyboard > div > div.keyboard-toolbar {
    display: none;
}
[data-tooltip]:after {
    display: none;
}

.ML__keyboard div .rows ul li.keycap:active,
.ML__keyboard div .rows ul li.keycap.active,
.ML__keyboard div .rows ul li.action:active,
.ML__keyboard div .rows ul li.action.active {
    transform: none;
}
.ML__keyboard div .rows ul li.keycap:active,
.ML__keyboard div .rows ul li.keycap.active {
    background-color: rgba(24, 101, 242, 0.37);
}
.ML__keyboard div .rows ul li.keycap.btn {
    background-color: #F6F6F7;
}
.ML__keyboard div .rows ul li.keycap.btn:active,
.ML__keyboard div .rows ul li.keycap.btn.active {
    background-color: #B9BDC6;
}
.ML__keyboard div .rows ul li.action:active,
.ML__keyboard div .rows ul li.action.active {
    background-color: #93969c;
}

.popup {
    -webkit-border-radius: 4px;
    -moz-border-radius: 4px;
    border-radius: 4px;
    border: 1px solid #000000;
    padding: 5px;
    position: absolute;
    left: 15px;
    bottom: -50px;

}
.popup .btn {
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    border-radius: 5px;
    border: 1px solid #93969C;
    padding: 9px;
    font-size: 14px;
    background-color: #fff;
    cursor: pointer;
    margin-right: 5px;
    position: relative;
}
.popup .btn:last-child {
    margin-right: 0;
}
.popup .btn:hover{
    background-color: #eaeaea;
}
.popup .btn span {
    font-size: 10px;
    opacity: 0.5;
    position: absolute;
    right: 2px;
    bottom: 0;
}

.grid {
    display: grid;
    grid-template-rows: repeat(4, 1fr);
    grid-auto-flow: column;
    grid-gap: 6px;
    margin: 0 auto 5px;
}
.keycap, .action {
    width: 40px;
    padding: 8px 0 7px;
    background-color: #fff;
    border: none;
    font-size: 16px;
    outline: none;
    -webkit-border-radius: 4px;
    -moz-border-radius: 4px;
    border-radius: 4px;
    box-shadow: 0px 1px 0px rgba(33, 36, 44, 0.32);
}
.keycap.active {
    background-color: #B9BDC6;
}
.action {
    background-color: #B9BDC6;
}
